.card {
  backdrop-filter: blur(20px) saturate(180%);
  -webkit-backdrop-filter: blur(20px) saturate(180%);
  background-color: #ffffff80;
  border-radius: 12px;
  border: 1px solid rgba(209, 213, 219, 0.3);
  width: 100px;
  height: 100px;
  align-items: center;
  justify-content: center;
}



.main-box{
  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-columns: repeat(3, 2fr);
  grid-template-rows: repeat(3, 200px);

}

input {
  background-color: transparent;
  box-shadow: none;
  width: 90px;
  text-align: center;
  justify-content: center;
  align-items: center;
  height: 70px;
  color: black;
  font-size: 10vh;
  font-weight: lighter;
  border-color: transparent;
  outline: none;
}

input:focus{
  font-weight: bolder;
}

.container{
  display: flex;
}

.container2{
  display: flex;
}

.main{
  width: 100px;
  height: 40px;
  justify-content: center;
  align-items: center;
  margin: auto;
  padding: auto;
  background-color: transparent;

  backdrop-filter: blur(20px) saturate(180%);
  -webkit-backdrop-filter: blur(20px) saturate(180%);
  background-color: #ffffff80;
  border-radius: 12px;
  border: 1px solid rgba(209, 213, 219, 0.3);

  font-weight: bolder

}
.main:hover{
  background-color: rgba(0, 0, 0, 0.9);
  color: white;
  backdrop-filter: blur(100px) saturate(180%);
  -webkit-backdrop-filter: blur(100px) saturate(180%);
}

.text{
  padding-top: 10px;
  color: black;
  font-size: 25px;
  font-weight: bolder;
  justify-content: center;
  align-items: center;
  margin: auto;
  padding: auto;
}


.eigenAns{
  backdrop-filter: blur(20px) saturate(180%);
  -webkit-backdrop-filter: blur(20px) saturate(180%);
  background-color: #ffffff80;
  border-radius: 12px;
  border: 1px solid rgba(209, 213, 219, 0.3);
  align-items: center;
  justify-content: center;
}

.sValues{
  width: 60px;
  height: 30px;
  text-align: center;
  font-size: 19px;
} 

.allsValues{
  display: grid;
  justify-items: center;
  grid-template-columns: repeat(3,2fr);
  padding: 20px;
}

.answer_main{
  display: inline-flexbox;
  text-align: center;
  font-size: 20px;
}

.eigenValues{
  display: grid;
  justify-items: center;
  grid-template-columns: repeat(3, 2fr);
  padding-top: 20px;
}

.eigenValue{
  width: 10px;
}

.charEqu{
  display: grid;
  justify-items: center;
  padding: 20px;
}

.char{
  width: 140px;
}

.quadEqu{
  display: grid;
  justify-items: center;
  padding: 20px;
}

.quad{
  width: 12vh;
}

.eigenVectorNode{
  display: grid;
  justify-content: center;
  padding-top: 20px;
  grid-template-columns: 2fr 2fr 2fr 2fr 4fr;
}



.eigenVectors{
  width: 30px;
  height: 30px;
  justify-self: center;
  align-items: center;
  text-align: center;
}

.eigenTitle{
  font-size: larger;
}

.test{
  width: auto;
}